@import "_colors.scss";
/******* menu **********/
header,
nav{
    @include phone{
        display: block;
        z-index: 12;// voir pour une modification 
       background-color: red;
    }
    .kalys_nav_menu  {
        @include phone {
          display: block;
         
        }
    }
    .kalys-collapse {
        @include phone {
            display: block;
            font-size: 1rem;
            right: 0;
            margin: 10px 0 0 0;
            float: left;
            z-index: 12;
        }
       ul.kalys_primary_nav {
            @include phone {
                margin-right: 10px;
                display: block;
            }
        }
    }
    .nav_logo {
        @include phone {
            top: 0;
            height: 75px;
            left: 0;
        }
        img {
            @include phone {
                width: 120px;
                height: 75px;
            }
        }
    }
    ul.kalys_primary_nav {
        @include phone {
            margin-right: 10px;
            display: block;
        }
        li:nth-child(-n + 4) {
            @include phone {
                margin-right: 10px;
                display: block;
            }
        }
    }
   .sticky {
        @include tablet{
            position: static;
        }
        @include phone {
            position: static;
            background-color: red; // test //
        }
    }
}
button {
    @include phone {
        width: 80px;
    }
}

div.kalys-collapse {
    @include phone {
        background-color: $title-color;
        border-radius: 10px;
        font-size: 2rem;
        text-align: center;
        width: 250px;
    }
}
/******** burger*/
a.icone_responsive {
    display: none;
    @include tablet {
        z-index: 9999;
        position: relative;
        display: block;
        width: 80px;
        height: 55px;
        background: url("http://localhost/kalys/wp-content/themes/kalysnailsandbeauty/assets/logo/ic√¥ne_burger.svg")
            no-repeat;
        background-size: contain;
        //box-shadow: 5px 5px 20px $color-shadow;
        cursor: pointer;
    }
}
