/********* Homepage****************/
.home {
    main {
        .kalys-col-full {
            /*******resposive accueil*****/
            @include tablet {
                box-shadow: none;
            }

            @include phone {
                box-shadow: none;
            }
        }
    }
    /******** Layout calendar Homepage*********/
    div.bookly-bootstrap {
        div.justify-content-start {
            @include phone {
                display: grid;
            }
        }
    }
}

/************* Blog/ news ***********/
.blog,
.page-template-single-soin-mains {
    #rev_slider_8_1_forcefullwidth,
    #rev_slider_7_1_forcefullwidth {
        @include tablet {
            margin-bottom: 0 !important;
        }
    }
    .kalys-post-manucure {
        @include phone {
            display: inline-block;
        }
    }
    .zoom-img-card-hover {
        @include phone {
            padding-right: 0;
        }
    }
    .full-article {
        @include phone {
            margin-left: 0;
            margin-right: 0;
        }
    }
}
.blog {
    div.news-list {
        @include phone {
            display: block;
            padding: 10px;
        }
    }
    article.home-news {
        @include phone {
            margin-bottom: 10px;
        }
    }
    .news-sidebar {
        @include phone {
            display: none;
        }
    }
}
